.components-waiting:before, .components-waiting:after,
.components-waiting span {
  -webkit-animation: waiting 0.3s infinite alternate;
          animation: waiting 0.3s infinite alternate;
  background: #00a862;
  -webkit-border-radius: 50%;
          border-radius: 50%;
  content: "";
  float: left;
  height: 8px;
  margin: 0 3px;
  vertical-align: middle;
  width: 8px; }

.components-waiting span {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s; }

.components-waiting:after {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s; }

@-webkit-keyframes waiting {
  0% {
    opacity: 1;
    -webkit-transform: scale(1.5);
            transform: scale(1.5); }
  100% {
    opacity: 0.4;
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes waiting {
  0% {
    opacity: 1;
    -webkit-transform: scale(1.5);
            transform: scale(1.5); }
  100% {
    opacity: 0.4;
    -webkit-transform: scale(1);
            transform: scale(1); } }
